@import url('https://cdn.jsdelivr.net/npm/bootstrap@5.1.3/dist/css/bootstrap.min.css');

::-webkit-scrollbar {
    width: 6px;
}

::-webkit-scrollbar-track {
    background: rgb(190, 190, 190);
    border-radius: 20px;
}

::-webkit-scrollbar-thumb {
    background-color: #000;
    border-radius: 20px;
}